@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
	font-family: 'Nunito Sans', sans-serif;
}
.container {
	max-width: 1180px;
	margin: 0 auto;
}
h1, h2, h3 {
	margin: 0
}
div, span, a, button, header, footer {
	box-sizing: border-box;
}
a {
	color: #333333;
	text-decoration: none;
}
ul {
	list-style: none;
}
.header {
	padding: 49px 0;
	min-height: 154px;
	height: 5vh;
}
.header .container {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.header__logo {
	font-weight: bold;
	font-size: 25px;
}
.header__menu {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.header__menu .active  {
	color: #004FEC;
}
.menu__item {
	font-size: 18px;
	margin-left: 35px;
}
.footer {
	height: 5vh;
	min-height: 173px;
	padding-bottom: 45px;
	padding-top: 60px;
}
.footer .container {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.footer__logo {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}
.footer span {
	display: inline-block;
}
.logo__title {
	font-size: 25px;
	font-weight: bold;
	margin-bottom: 12px;
}
.footer__text {
	color: #333d51;
	font-size: 15px;
	text-align: center;
}
.footer__info {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}
.info__made {
	font-size: 12px;
	text-align: right;
	color: #333d51;
	margin-bottom: 8px;
}
.info__author {
	color: #333d51;
	font-size: 24px;
	font-weight: bold;
}
.blue-text {
	color: #004FEC;
}
.main__title {
	font-size: 60px;
	line-height: 93%;
	margin-bottom: 75px;
}
.news__subscribe {
	display: inline-block;
	margin-top: 60px;
}
.news__item {
	width: 100%;
	max-width: 350px;
	height: 290px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	padding: 24px 0;
	cursor: pointer;
}
.news__item:hover .news__title::before {
	width: 350px;
}
.news__item:hover .news__title {
	color: #ffffff;
}
.news__title {
	font-size: 24px;
	line-height: 30px;
	padding: 0 23px;
	position: relative;
	transition: color .2s ease;
}
.news__title::before {
	content: '';
	height: 100%;
	width: 2px;
	background-color: #004FEC;
	transition: width .4s ease-in-out;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
	
}
.new__info {
	width: 100%;
	padding: 0 23px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.info__target {
	font-size: 20px;
	color: rgba(51, 61, 81, 0.5);
}
.info__date {
	display: flex;
	align-items: flex-end;
	color: rgba(51, 61, 81, 0.5);
}
.date__date {
	font-size: 30px;
	color: #333d51;
	line-height: 30px;
}
.date__months {
	font-size: 20px;
	color: rgba(51, 61, 81, 0.5);
}
.news__list {
	display: grid;
	grid-template-columns: repeat(3, 350px);
	grid-gap: 20px;
}
.news__detail {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
}
.detail__info {
	width: 100%;
	max-width: 350px;
}
.detail__title {
    font-size: 29px;
    line-height: 65px;
}
.detail__source {
	display: inline-block;
	font-size: 25px;
	color: rgba(51, 61, 81, 0.5);
	margin-bottom: 40px;
}
.detail__content {
	width: 100%;
	max-width: 730px;
}
img {
	width: 100%;
	height: 100%;
}
.detail__image {
	margin-bottom: 60px;
}
.detail__text {
	font-size: 25px;
	line-height: 35px;
}

.contacts__block {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
}
.contacts__image {
	width: 100%;
	max-width: 540px;
	height: 340px;
}
.contacts__info {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}
.contacts__tel {
	font-size: 60px;
	margin-bottom: 60px;
}
.contacts__fio {
	font-size: 25px;
	line-height: 30px;
	border-left: 2px solid #004FEC;
	padding-left: 23px;
	margin-bottom: 25px;
}
.contacts__mail {
	font-size: 20px;
	margin-bottom: 52px;
}
.contacts__position {
	color: rgba(51, 61, 81, 0.5);
	font-size: 20px;
	margin-bottom: 25px;
}
.contacts__skills {
	display: flex;
	align-items: center;
	font-size: 20px;
}
